.card-accordion {
  height: 100%;
}

.GridViewScrollHeader {
  background-color: #39BAF0;
	TH {
		padding: 20px 0 48px 15px;
		font-weight: normal;
		white-space: nowrap;
		text-align: left;
    vertical-align: bottom;
    border-spacing: 0;

    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.005em;
    color: #FFFFFF;
    background-color: #39BAF0;

    &:nth-of-type(2) {
      padding-left: 0;
    }
	}
}
.GridViewScrollItem {
	TD {
    position: relative;
		padding: 16px 0 16px 15px;
		white-space: nowrap;
		background-color: #FFFFFF;


    font-size: 11px;
    line-height: 14px;
    color: #323B47;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 0;
      width: 100%;
      height: 1px;
      border-bottom: 1px solid rgba($color: #323b47, $alpha: 0.2);
    }
  }

  &:last-of-type {
    td {
      display: flex;
    }
  }


  td:nth-of-type(2) {
    padding-left: 0;
  }

  td:last-of-type {
    &::after {
      width: calc(100% - 15px);
    }
  }

}
.GridViewScrollItemFreeze {
	TD {
    position: relative;
		padding: 16px 0 16px 15px;
		white-space: nowrap;

		background-color: #FFFFFF;


    font-size: 11px;
    line-height: 14px;
    color: #323B47;

    &::after {
      content: "";
      position: absolute;
      left: 15px;
      bottom: 0;
      z-index: 0;
      width: 100%;
      height: 1px;
      border-bottom: 1px solid rgba($color: #323b47, $alpha: 0.2);
    }
  }

  TD.specifications__subtitle {
    color: #39baf0;
  }

  &:last-of-type {
    td div {
      display: flex;
    }
  }
}
