.catalog {
  @include mobile {
    margin-bottom: 22px;

    h1 {
      margin-top: -42px;
      padding-left: 28px;
      margin-bottom: 10px;

      @include mobile {
        margin-top: -44px;
      }
    }
  }

  &__wrap {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;

    @include mobile {
      position: relative;
      flex-direction: column;
    }
  }

  & aside {
    margin-right: 3.6%;

    @include tablet {
      margin-right: 4.5%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    min-width: 70%;
    max-width: 70%;
    margin-top: 24px;

    @include tablet {
      min-width: 66%;
      max-width: 66%;
      margin-top: 0;
    }

    @include mobile {
      flex-direction: column;
      min-width: 100%;
      max-width: 100%;
    }
  }

  &__item {
    min-width: 44.7%;
    max-width: 44.7%;
    margin-bottom: 58px;
    margin-right: 3%;

    &:nth-of-type(2n) {
      margin-right: 0;
    }

    @include tablet {
      min-width: 48%;
      max-width: 48%;
      margin-bottom: 53px;
    }

    @include tablet-1200px {
      margin-bottom: 40px;
    }

    @include tablet-inner {
      margin-bottom: 40px;
    }

    @include mobile {
      min-width: 100%;
      max-width: 100%;
      margin-bottom: 9px;
    }
  }

  &__link-img {
    display: block;
    width: 100%;
    margin-bottom: 24px;
    transition: all 0.2s ease;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  &__img-wrap {
    position: relative;
    margin-bottom: 25px;
    display: block;
    padding-top: 70%;
    overflow: hidden;

    img {
      object-fit: cover;
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;

      @include tablet {
        margin-bottom: 21px;
      }

      @include mobile {
        min-width: 100%;
        margin-bottom: 10px;
      }
    }

    @include tablet {
      margin-bottom: 20px;
    }
  }


  &__link-text {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 24px;
    line-height: 36px;
    color: $dark;

    @include tablet-1440px {
      font-size: 22px;
      line-height: 22px;
    }

    @include tablet {
      line-height: 26px;
    }

    @include tablet-1200px {
      font-size: 20px;
      line-height: 20px;
    }

    @include tablet-inner {
      font-size: 18px;
      line-height: 18px;
    }

    @include mobile {
      line-height: 28px;
    }
  }
}
